            animation: btn-new-hover 250ms;
}

.btn-new:hover{
    -webkit-transform: scale(1.09);
            transform: scale(1.09)
}

@-webkit-keyframes btn-new-hover{

    25%{
        -webkit-transform: rotate(-15deg);
                transform: rotate(-15deg)
    }
    75%{
        -webkit-transform: rotate(15deg);
                transform: rotate(15deg)
    }
}

@keyframes btn-new-hover{

    25%{
        -webkit-transform: rotate(-15deg);
                transform: rotate(-15deg)
    }
    75%{
        -webkit-transform: rotate(15deg);
                transform: rotate(15deg)
    }
}

