@-webkit-keyframes timer-color{
    0% {  color: #202020;}
    70% {  color: #202020;
        -webkit-transform: scale(1);
                transform: scale(1)}
    100% {  color: #d30000;
            -webkit-transform: scale(1.1);
                    transform: scale(1.1)
            }
}

@keyframes timer-color{
    0% {  color: #202020;}
