.check-wrapper{
    display: flex;
    justify-content: center;
}

 .path{
    width:40%;
    stroke: aliceblue;
    stroke-width: 20%;
    stroke-dasharray: 2500;
    opacity: 10;
  }
  .check{
    -webkit-animation: checkmark 800ms cubic-bezier(0,0.23,1,.1);
            animation: checkmark 800ms cubic-bezier(0,0.23,1,.1);
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
  }
  .xmark{
    -webkit-animation: xmad 500ms cubic-bezier(0,0.23,1,.1);
            animation: xmad 500ms cubic-bezier(0,0.23,1,.1);
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
  }
  
  
  @-webkit-keyframes xmad {
    0% {
        stroke-dashoffset: 2500;
    }
    80%{
      -webkit-transform: scale(1);
              transform: scale(1)
    }
    90% {
        opacity: 10;
        stroke-dashoffset: 0;
    }
    100% { -webkit-transform: translate(20px, -2px); transform: translate(20px, -2px)}
  }
  
  
  @keyframes xmad {
    0% {
        stroke-dashoffset: 2500;
    }
    80%{
      -webkit-transform: scale(1);
              transform: scale(1)
    }
    90% {
        opacity: 10;
        stroke-dashoffset: 0;
    }
    100% { -webkit-transform: translate(20px, -2px); transform: translate(20px, -2px)}
  }
  
  
  @-webkit-keyframes checkmark {
    0% {
        stroke-dashoffset: 2500;
    }
    90%{
      -webkit-transform: scale(1.09);
              transform: scale(1.09)
    }
    100% {
        opacity: 10;
        stroke-dashoffset: 0;
    }
  }
  
  
  @keyframes checkmark {
    0% {
        stroke-dashoffset: 2500;
    }
    90%{
      -webkit-transform: scale(1.09);
              transform: scale(1.09)
    }
    100% {
        opacity: 10;
        stroke-dashoffset: 0;
    }
  }
  


