  .zoom{
    position: absolute;
    left: -500px;
    -webkit-animation: zooms 3000ms cubic-bezier(0,0.23,1,.1);
            animation: zooms 3000ms cubic-bezier(0,0.23,1,.1);
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
  }
  
  .jeremy{
    position: absolute;
    left: -700px;
    -webkit-animation: jeremy 5500ms cubic-bezier(0,0.23,1,.1);
            animation: jeremy 5500ms cubic-bezier(0,0.23,1,.1);
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
  }
  
  @-webkit-keyframes zooms {
    0% {
        -webkit-transform: translate(3000px, -100px);
                transform: translate(3000px, -100px)
    }
    100% {
      -webkit-transform: scale(1.09);
              transform: scale(1.09)
    }
  }
  
  @keyframes zooms {
    0% {
        -webkit-transform: translate(3000px, -100px);
                transform: translate(3000px, -100px)
    }
    100% {
      -webkit-transform: scale(1.09);
              transform: scale(1.09)
    }
  }
  
  @-webkit-keyframes jeremy {
